'use client'

import { useEffect, useState, useRef } from 'react'

export function HeroSection() {
  const [mousePosition, setMousePosition] = useState({ x: 0, y: 0 })
  const containerRef = useRef<HTMLDivElement>(null)

  useEffect(() => {
    const handleMouseMove = (e: MouseEvent) => {
      if (containerRef.current) {
        const rect = containerRef.current.getBoundingClientRect()
        setMousePosition({
          x: (e.clientX - rect.left) / rect.width,
          y: (e.clientY - rect.top) / rect.height,
        })
      }
    }

    window.addEventListener('mousemove', handleMouseMove)
    return () => window.removeEventListener('mousemove', handleMouseMove)
  }, [])

  return (
    <section
      id="home"
      ref={containerRef}
      className="min-h-screen flex items-center justify-center pt-20 relative overflow-hidden"
    >
      {/* Animated gradient background */}
      <div 
        className="absolute inset-0 opacity-40"
        style={{
          background: `radial-gradient(circle 800px at ${mousePosition.x * 100}% ${mousePosition.y * 100}%, rgba(255, 255, 255, 0.15), transparent 50%)`,
        }}
      />
      <div 
        className="absolute inset-0 opacity-30"
        style={{
          background: `radial-gradient(circle 600px at ${(1 - mousePosition.x) * 100}% ${(1 - mousePosition.y) * 100}%, rgba(156, 163, 175, 0.2), transparent 50%)`,
        }}
      />
      <div className="absolute inset-0 bg-gradient-to-br from-gray-400/5 via-transparent to-gray-500/10" />
      
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 w-full">
        <div className="animate-slide-in-left space-y-6 w-full">
          <div className="space-y-2">
            <div className="text-sm font-medium text-muted-foreground uppercase tracking-wider">
              Welcome to my portfolio
            </div>
            <h1 className="text-5xl md:text-6xl lg:text-7xl font-black tracking-tighter leading-tight">
              Full-Stack
              <br />
              <span className="bg-gradient-to-r from-foreground via-muted-foreground to-foreground bg-clip-text text-transparent">
                Developer
              </span>
            </h1>
          </div>

          <p className="text-lg text-muted-foreground leading-relaxed">
            Building futuristic web experiences with cutting-edge technologies. Specialized in MERN stack development with a passion for clean code and seamless user experiences.
          </p>

          <div className="flex gap-4 pt-4">
            
            <button className="px-8 py-3 border border-foreground text-foreground font-semibold rounded-lg hover:bg-muted transition-all duration-300">
              
              Get in Touch
            </button>
            <a href='/cv.pdf' download="My_Cv.pdf" className="px-8 py-3 bg-foreground text-background font-semibold rounded-lg hover:shadow-lg transition-all duration-300 hover:scale-105 active:scale-95">
              Download Resume
            </a>
          </div>
        </div>
      </div>
    </section>
  )
}